import React from 'react'
import {URL} from '../../publics/config'
import { Card } from "react-bootstrap"
import { Redirect } from 'react-router-dom'
class ProductCard extends React.Component{
  constructor(props){
    super(props)
    this.state ={
      quantity : 0,
      redirectToDetails:false
    }
    this.redirectToDetails = this.redirectToDetails.bind(this)
  }
  redirectToDetails = () => {
    this.setState({redirectToDetails:true})
  }
  render(){
    if (this.state.redirectToDetails)
    return <Redirect to={`/home/products/${this.props.product._id}`}/>
      const {name, image,stock} = this.props.product
      // const imageUrl = `${URL}/products/images/${image}`
      return(

        <div className='border col-3 border col-3 mx-4 mt-5 mb-3 p-0 card' onClick={() => this.redirectToDetails()}>
          <div className="text-center">
            <img src={image} onError={(e) => {e.target.oneerror =null; e.target.src = 'https://icon-library.net/images/inventory-icon/inventory-icon-10.jpg'}}  className="img-fluid d-inline-block img-h" />
          </div>
          <div className="p-3">
            <h5>{name}</h5>
            <p>stock: {stock}</p>
          </div>
        </div>

        )
  }
}
export default ProductCard
