import React,{Fragment} from 'react'
import {Row, Col, Form, Button, Modal} from 'react-bootstrap'
import {connect} from 'react-redux'

import {addProduct} from '../../publics/actions/Products'
import {getCategories} from '../../publics/actions/Categories'

class FormCreateProduct extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            categories : [],
            formData : {
                name: '',
                description:'',
                categoriesId:'',
                stock:0,
                image: ''
            },
            // filename: null,
            showModal:false,
            modalTitle:"",
            modalMessage:"",
            history:props.history
        }
        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
        this.handleClose  = this.handleClose.bind(this)
        // this.onChange = this.onChange.bind(this)
    }

    handleClose = () => {
        this.setState({showModal: false})
        if (this.state.redirectOnCloseModal)
        this.props.history.push('/')
    }

    handleChange(event){
        let newFormData = {...this.state.formData}
        const target = event.target
        const name = target.name
        const value = target.value
        newFormData[name] = value
        this.setState({
            formData: newFormData
        })
        console.log(this.state.formData)
    }

    // onChange(e){
    //   this.setState({filename: e.target.files})
    // }

    handleSubmit = async (event) => {
        event.preventDefault();
        // const filedata = new FormData()
        // filedata.append('file', this.state.filename)
        // console.log('isi file')
        // console.log(this.state.filename)
        // console.log(filedata)
        // const data = {
        //   name : this.state.formData.name,
        //   description : this.state.formData.description,
        //   categoryId : this.state.formData.categoryId,
        //   stock : this.state.formData.stock,
        //   image : this.state.filename
        // }
        await this.props.dispatch(addProduct(this.state.formData))
        .then(()=>{this.setState({
            showModal: true,
            modalTitle:"Success",
            modalMessage:"Product successfully added!",
            redirectOnCloseModal: true
        })})
        .catch(()=>{
          this.setState({
          showModal: true,
          modalTitle:"Failed",
          modalMessage:this.props.product.errMessage,
        })})
    }

    componentDidMount = async () => {
        await this.props.dispatch(getCategories())
        this.setState ({categories: this.props.category.categories})
    }

    render(){
        const {categories} = this.state
        return(
            <Fragment>
                <Form onSubmit={this.handleSubmit}>

                    <Form.Group as={Row} controlId="formPlaintextName">
                        <Form.Label column sm="2">
                            Name
                        </Form.Label>
                        <Col sm="10">
                        <Form.Control onChange={this.handleChange} type="text" name="name" placeholder="Name product" required/>
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} controlId="formPlaintextDescription">
                        <Form.Label column sm="2">
                            Description
                        </Form.Label>
                        <Col sm="10">
                        <Form.Control onChange={this.handleChange} type="text" name="description" placeholder="Description..." required/>
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} controlId="formPlaintextImageURL">
                        <Form.Label column sm="2">
                            Image
                        </Form.Label>
                        <Col sm="10">
                        <Form.Control onChange={this.handleChange} type="text" name="image"  placeholder="Image URL..." required/>
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} controlId="formPlaintextDateReleased">
                        <Form.Label column sm="2">
                            Stock
                        </Form.Label>
                        <Col sm="10">
                        <Form.Control onChange={this.handleChange} name="stock" type="number"  placeholder="stock..." min="0" required/>
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} controlId="formPlaintextCategory">
                        <Form.Label column sm="2">Category</Form.Label>
                        <Col sm="10">
                            <Form.Control onChange={this.handleChange} as="select" name="categoriesId" required>
                                <option>--Select Category--</option>
                                {
                                    categories.lenght !== 0 ? categories.map((category)=>{
                                    return <option value={category._id} key={category._id}> {category.name} </option>
                                    }) : <option>Loading ... </option>
                                }
                            </Form.Control>
                        </Col>
                    </Form.Group>

                    <Button style={{float:"right"}} variant="warning" type="submit">
                        Save
                    </Button>
                </Form>

                <Modal show={this.state.showModal} onHide={this.handleClose}>
                    <Modal.Header>
                        <Modal.Title>{this.state.modalTitle}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>{this.state.modalMessage}</Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.handleClose}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Modal>
            </Fragment>
        )
    }

}
const mapStateToProps = state => {
    return{
      product: state.product,
      category: state.category
    }
  }

  export default connect(mapStateToProps)(FormCreateProduct)
